  import uniNumberBox from '@/colorui/components/uni-number-box.vue'

  export default {
    components: {
      uniNumberBox
    },
    data() {
      return {
        list: [],
				tags:[{name:'餐厅A'},{name:'餐厅B'}],
        tabCur: 0,
				listData:[]
      };
    },
    computed: {
      // total() {
      //   let data = this.listData[0].map(res => res.num)
      //   let num = 0
      //   data.forEach(res => {
      //     num += res
      //   })
      //   return num
      // }
    },
    mounted() {
      uni.showLoading({
        title: '加载中...',
        mask: true
      });
      let list = [{}];
      for (let i = 0; i < 26; i++) {
        list[i] = {};
        list[i].name =  '套餐' + i;
        list[i].id = i;
        list[i].kouwei = true;
        list[i].isHot = '辣';
        list[i].num = 0
      }
      this.listData[0] = list;
      this.listData[1] = [{name:"套餐12"}];
      this.list = this.listData[0];
      uni.hideLoading()
    },
    methods: {
      submit() {
        let data = this.listData[0].filter(res => res.num)
        let str = ''
        data.forEach(res => {
          str += `${res.name}${res.isHot?`(${res.isHot})`:''}*${res.num};\n`
        })
        uni.showModal({
          title: '提示',
          content: `你点了${str}`,
          success: function (res) {
            if (res.confirm) {
              console.log(data)
            }
          }
        });
      },
      radioChange(item, la) {
        this.$nextTick(()=>{
          item.isHot = la
        })
			},
      TabSelect(e) {
        this.tabCur = e.currentTarget.dataset.id;
        this.list = this.listData[this.tabCur]
      },
      VerticalMain(e) {
      }
    },
  }
